<template>
  <div class="card">
    <div class="card-header bg-dark text-white">
      <div class="row">
        <div class="col d-flex justify-content-between">
          <div>
            {{ titulo }}
          </div>
          <div class="form-check form-switch">
            <input
              type="checkbox"
              role="switch"
              class="form-check-input"
              v-model="checked"
            />
            <label for="" class="form-check-label">Favoritar</label>
          </div>
        </div>
      </div>
    </div>
    <div class="card-body">
      <p>{{ descricao }}</p>
    </div>
    <div class="card-footer">
      <small class="text-muted">
        Salário: R$ {{ salario }} | Modalidade: {{ getModalidade }} | Tipo:
        {{ getTipo }} | Publicação: {{ getPublicacao }}
      </small>
    </div>
  </div>
</template>

<script>
export default {
  name: "VagaCard",
  props: {
    titulo: {
      type: String,
      required: true,
    },
    descricao: {
      type: String,
      required: true,
    },
    salario: {
      type: Number,
      required: true,
    },
    modalidade: {
      type: String,
      required: true,
    },
    tipo: {
      type: String,
      required: true,
    },
    publicacao: {
      type: String,
      required: true,
    },
    favoritada: {
      type: Boolean,
      default: false,
    },
  },
  data: () => ({
    checked: false,
  }),
  watch: {
    checked(novoValor) {
      if (novoValor) {
        this.emitter.emit("favoritarVaga", {
          titulo: this.titulo,
          favoritada: novoValor,
        });
      } else {
        this.emitter.emit("desfavoritarVaga", {
          titulo: this.titulo,
          favoritada: novoValor,
        });
      }
    },
  },
  mounted() {
    if (this.favoritada) {
      this.checked = true;
    }
  },
  beforeUpdate() {
    const vagas = JSON.parse(localStorage.getItem("vagas"));
    let indexStorage = vagas
      .map((vagaStorage) => vagaStorage.titulo)
      .indexOf(this.titulo);

    let vagaStorage = vagas[indexStorage];
    this.checked = vagaStorage.favoritada;
  },
  computed: {
    getModalidade() {
      switch (this.modalidade) {
        case "1":
          return "Home Office";
        case "2":
          return "Presencial";
      }
      return "";
    },
    getTipo() {
      switch (this.tipo) {
        case "1":
          return "CLT";
        case "2":
          return "PJ";
      }
      return "";
    },
    getPublicacao() {
      let dataPublicacao = new Date(this.publicacao);
      return dataPublicacao.toLocaleDateString("pt-BR");
    },
  },
};
</script>
